<script setup>
import BaseHeader from '@/components/common/header/baseHeader.vue'
import BaseFooter from '@/components/common/footer/baseFooter.vue'

//import { useStore } from "vuex";

import axios from "axios";
import { ref, onBeforeMount} from 'vue';

    onBeforeMount(() => {
        getScheduleList();
    });

//   const store = useStore();

    const yyyymmdd = ref('');
    const weekLoop = ref([]);

    let nowYear;
    let nowMon;

    const getScheduleList = async (param) => {
//        store.commit("changeLoadingStatus", true);

        await axios.post("/scheduleCalender.do",{yyyymmdd:param})
        .then((res) => {
            console.log('조회결과',res.data);
            weekLoop.value = res.data.RESULT_LIST;
            yyyymmdd.value = res.data.YYYYMMDD;
            nowYear = yyyymmdd.value.substring(0,4);
            nowMon = yyyymmdd.value.substring(5,7);

        })
        .catch((res) => {
           console.error("에러", res);
        });
 //       store.commit("changeLoadingStatus", false);
    }

    function preMonth() {
        let param = '';
        if(Number(nowMon)==1) {
            param = Number(nowYear)-1 +'-12';
        } else if (Number(nowMon) < 11 ) {
            param = nowYear + '-0' + (Number(nowMon)-1);
        } else {
            param = nowYear + '-' + (Number(nowMon)-1);
        }
        getScheduleList(param);
    }

    function nextMonth() {
        let param = '';
        if(Number(nowMon)==12) {
            param = Number(nowYear)+1 +'-01';
        } else if (Number(nowMon) < 9 ) {
            param = nowYear + '-0' + (Number(nowMon)+1);
        } else {
            param = nowYear + '-' + (Number(nowMon)+1);
        }
        getScheduleList(param);
    }

    function rowData(row) {
        if(row == '') {
            return '';
        } else {
            let temp = row.split("|");
            return temp[0];
        }
    }

    function linkRowData(row) {
        if(row == '') {
            return '';
        } else {
            let temp = row.split("|")[0];

            if(Number(temp) < 10) {
                temp = '0'+temp;
            } 
            return yyyymmdd.value.replace("-","")+temp;
        }
    }


    let rowDataLength = 0;

    function rowDataSchedule(row) {
        if(row == '') {
            return '';
        } else {
            let temp = row.split("|");
            let retArray = temp[1].split("@");
            rowDataLength = retArray.length-1;
            return retArray;
        }
    }

    function checkBR(row) {
        if(row == '') {
            return false;
        } else return true;
    }
</script>

<template>
  <BaseHeader title="스케쥴"></BaseHeader>
  <div>
    <main>
    <div>
        <div>
            <button type="button" title="이전달" @click="preMonth"><span >이전달</span></button>
            <span>{{yyyymmdd}}</span>
            <button type="button" title="다음달" @click="nextMonth"><span >다음달</span></button>
        </div>

        <table>
            <thead>
                <tr>
                    <th>일</th>
                    <th>월</th>
                    <th>화</th>
                    <th>수</th>
                    <th>목</th>
                    <th>금</th>
                    <th>토</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="row in weekLoop" v-bind:key="row">
                    <td><RouterLink :to="{ path: '/scheduleOneDay/'+linkRowData(row.SUN)}">{{ rowData(row.SUN) }}</RouterLink> <br v-if="checkBR(row.SUN)"> 
                        <template v-for="(rowData, index) in rowDataSchedule(row.SUN)" v-bind:key="index">
                            {{ rowData }} <br v-if="index < rowDataLength ">
                        </template>
                    </td>
                    <td><RouterLink :to="{ path: '/scheduleOneDay/'+linkRowData(row.MON)}">{{ rowData(row.MON) }}</RouterLink> <br v-if="checkBR(row.MON)"> 
                        <template v-for="(rowData, index) in rowDataSchedule(row.MON)" v-bind:key="index">
                            {{ rowData }}<br v-if="index < rowDataLength ">
                        </template>
                    </td>
                    <td><RouterLink :to="{ path: '/scheduleOneDay/'+linkRowData(row.TUE)}">{{ rowData(row.TUE) }}</RouterLink> <br v-if="checkBR(row.TUE)">
                        <template v-for="(rowData, index) in rowDataSchedule(row.TUE)" v-bind:key="index">
                            {{ rowData }}<br v-if="index < rowDataLength ">
                        </template>
                    </td>
                    <td><RouterLink :to="{ path: '/scheduleOneDay/'+linkRowData(row.WED)}">{{ rowData(row.WED) }}</RouterLink> <br v-if="checkBR(row.WED)">
                        <template v-for="(rowData, index) in rowDataSchedule(row.WED)" v-bind:key="index">
                            {{ rowData }}<br v-if="index < rowDataLength ">
                        </template>
                    </td>
                    <td><RouterLink :to="{ path: '/scheduleOneDay/'+linkRowData(row.THU)}">{{ rowData(row.THU) }}</RouterLink> <br v-if="checkBR(row.THU)">
                        <template v-for="(rowData, index) in rowDataSchedule(row.THU)" v-bind:key="index">
                            {{ rowData }}<br v-if="index < rowDataLength ">
                        </template>
                    </td>
                    <td><RouterLink :to="{ path: '/scheduleOneDay/'+linkRowData(row.FRI)}">{{ rowData(row.FRI) }}</RouterLink> <br v-if="checkBR(row.FRI)">
                        <template v-for="(rowData, index) in rowDataSchedule(row.FRI)" v-bind:key="index">
                            {{ rowData }}<br v-if="index < rowDataLength ">
                        </template>
                    </td>
                    <td><RouterLink :to="{ path: '/scheduleOneDay/'+linkRowData(row.SAT)}">{{ rowData(row.SAT) }}</RouterLink> <br v-if="checkBR(row.SAT)">
                        <template v-for="(rowData, index) in rowDataSchedule(row.SAT)" v-bind:key="index">
                            {{ rowData }}<br v-if="index < rowDataLength ">
                        </template>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
  </main>
</div>
<BaseFooter></BaseFooter>
</template>

<style>

table {
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
}
tr {
	height: 30px;
}
th,td {
	padding: 6px 15px;
}
th {
  background: #b096bb;
  color:#101ee7;
  text-align: left;
}
tr:first-child th:first-child {
  border-top-left-radius: 6px;
}
tr:first-child th:last-child {
  border-top-right-radius: 6px;
}
td {
  border-right: 1px solid #c6c9cc;
  border-bottom: 1px solid #c6c9cc;
}
td:first-child {
  border-left: 1px solid #c6c9cc;
  background: #f50303;
}
tr:nth-child(even) td {
  background: #eaeaed;
}
tr:last-child td:first-child {
  border-bottom-left-radius: 6px;
}
tr:last-child td:last-child {
  border-bottom-right-radius: 6px;
}
</style>